/* Custom gradient blur effects for hero section */
.gradient-blur-1 {
  background: radial-gradient(ellipse at center, rgba(100, 180, 255, 0.3) 0%, transparent 70%);
  filter: blur(60px);
}

.gradient-blur-2 {
  background: radial-gradient(ellipse at center, rgba(180, 100, 255, 0.25) 0%, transparent 70%);
  filter: blur(80px);
}

.gradient-blur-3 {
  background: radial-gradient(ellipse at center, rgba(255, 150, 200, 0.2) 0%, transparent 70%);
  filter: blur(50px);
  mix-blend-mode: hard-light;
}

.gradient-blur-4 {
  background: radial-gradient(ellipse at center, rgba(100, 200, 255, 0.3) 0%, transparent 70%);
  filter: blur(70px);
}

/* Section headings */
.section-heading {
  font-family: 'Noto Serif', serif;
  font-weight: 500;
  font-size: 80px;
  line-height: 1;
  color: #1c2122;
}

@media (max-width: 1024px) {
  .section-heading {
    font-size: 56px;
  }
}

@media (max-width: 640px) {
  .section-heading {
    font-size: 40px;
  }
}

.group:hover .group-hover\:text-text-dark-alt{
  --tw-text-opacity: 1;
  color: rgb(26 27 28 / var(--tw-text-opacity, 1));
}

@media (min-width: 640px){

  .sm\:h-\[400px\]{
    height: 400px;
  }
}

@media (min-width: 768px){

  .md\:grid-cols-3{
    grid-template-columns: repeat(3, minmax(0, 1fr));
